.wrapper{
  width: 300px;
  padding: 20px;
  margin-left: 30px;
  background-color: rgba(0, 0, 0, .65);
  border-radius: 5px;
  @media  screen and (max-width: 949px) {
    width: 100%;
    margin: 20px 0 0;
  }
}
.list{
  margin: 0;
  padding: 0;
  list-style-type: none;

}
.list__item{
  padding: 5px;
  margin-bottom: 7px;

}
.list__title{
  text-decoration: underline;
  text-shadow: 0 0 40px deepskyblue;
}